export default {
  name: '',
  components: {},
  data () {
    return {
      lists: [],
      lists1: []
    }
  },
  mounted () {
    let _this = this
    this.axios.get('web/fagclasslist').then(res => {
      if (res.success) {
        this.lists = res.data
        this.lists.map((item, index) => {
          item.show = false
        })
        return res.data
      }
    }).then((dataLists) => {
      dataLists.map((item, index) => {
        console.log('faqlist', item.id)
        this.axios.get('web/faqlist/' + item.id).then(res => {
          if (res.success) {
            console.log('faqlist', res)
            let lists1 = res.data
            lists1.map((val, key) => {
              val.checked = false
            })
            _this.lists1.push(lists1)
            _this.lists.some((item1, index1) => {
              if (item1.id === item.id) {
                /* result.map((item1, index1) => {
                  item1.checked = false
                }) */
                item1.lists1 = lists1
                // item.lists = result
                return true
              }
            })
          }
        })
      })
    }).catch(err => {
      console.log(err)
    })
  },
  methods: {
    expand (id, id1) { // 展开
      console.log(id, id1)
      this.lists.map((item, index) => {
        item.show = false
        console.log(item.id === id, item.id, id)
        if (item.id === id) {
          console.log('item.show ', item.show )
          item.show = !item.show
          item.lists1.some((item1, index1) => {
            if (item1.id === id1) {
              item1.checked = !item1.checked
            }
          })
        }
      })
    },
    getFaqList (id) {
      let _this = this
      let lists = []
      this.axios.get('web/faqlist/' + id).then(res => {
        if (res.success) {
          console.log('suuccess getFaqList', res)
          lists = res.data
          console.log(id)
          lists.map((item, index) => {
            item.show = false
            console.log(item.id)
            if (item.id === id) {
              item.show = !item.show
            }
          })
          /* lists.push({
            id: result
          }) */
          /* lists.some((item, index) => {
            if (item.id === id) {
              result.map((item1, index1) => {
                item1.checked = false
              })
              item.lists = result
              return true
            }
          }) */
          this.lists1 = lists
          console.log('getFaqList', lists)
        }
      })
    }
  }
}



